import { Component, OnInit } from '@angular/core';
import { Input } from '@angular/core';

@Component({
  selector: 'app-transform',
  templateUrl: './transform.component.html',
  styleUrls: ['./transform.component.css'],
})
export class TransformComponent implements OnInit {
  @Input() type: string;

  get title(): string {
    let value: string;
    switch (this.type) {
      case "s2l":
        value = "网址还原"
        break;

      case "l2s":
        value = "网址压缩"
        break;

      default:
        value = "无此类别"
        break;
    }
    return value;
  }

  get buttonValue(): string {
    let value: string;
    switch (this.type) {
      case "s2l":
        value = "获取"
        break;

      case "l2s":
        value = "生成"
        break;

      default:
        value = "无此类别"
        break;
    }
    return value;
  }

  get placeholder(): string {
    let value: string;
    switch (this.type) {
      case "s2l":
        value = "请输入转换后的网址或五位代码"
        break;

      case "l2s":
        value = "请输入需要转换的网址"
        break;

      default:
        value = "无此类别"
        break;
    }
    return value;
  }

  inputValue: string;
  outputValue: string = "";

  isLoading = false;

  async generate() {
    this.isLoading = true;

    if (!this.inputValue) {
      this.outputValue = "请输入网址";
      this.isLoading = false;
      return;
    }

    let apiPrefix: string = "";
    switch (this.type) {
      case "l2s":
        apiPrefix += "/short/";
        break;
      case "s2l":
        apiPrefix += "/long/";
        break;
    }

    let url = this.inputValue;
    url = encodeURIComponent(url);

    let mode = <RequestMode>"CORS";

    let init = {
      mode: mode,
    }
    let request = new Request(apiPrefix + url, init);

    try {
      let outputValuePrefix = "";
      if (this.type === "l2s") outputValuePrefix = location.origin + "/";
      let response = await fetch(request);
      if (!response.ok) throw new Error("#1");
      let json = await response.json();
      if (json.success) this.outputValue = outputValuePrefix + json.url;
      else this.outputValue = json.message;

    } catch (e) {
      switch (e.message) {
        case "#1":
          this.outputValue = "服务器无法处理您的请求。"
          break;
        case "#2":
          break;
        default:
          this.outputValue = "请求有问题，请稍后重试。"
          break;
      }
    } finally {
      this.isLoading = false;
    }
  }

  constructor() { }

  ngOnInit() {
  }

}



